<template>
  <div id="editPassword">
    <common-header title="修改登录密码"></common-header>
    <div class="input">
      <input type="password" placeholder="输入当前密码" v-model="oldPassword">
    </div>
    <div class="input">
      <input type="password" placeholder="输入新密码(6-15)字母和数字结合" v-model="newPassword">
    </div>
    <div class="input">
      <input type="password" placeholder="再次输入新密码" v-model="newPasswordSure">
    </div>
    <div class="finish__btn" :class="{'active': isFinish}" @click="submit">完成</div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        oldPassword: '',
        newPassword: '',
        newPasswordSure: ''
      }
    },

    computed: {
      isFinish() {
        if(!this.oldPassword || !this.newPassword || !this.newPasswordSure) {
          return false;
        }else {
          return true;
        }
      }
    },

    methods: {
      submit() {
        if(!isFinish) {
          return false;
        }

        if(this.newPassword != this.newPasswordSure) {
          this.$toast('新密码不一致');
          return false;
        }

        let param = {
          a: 1, 
          checkKey: '',
          password: this.oldPassword, 
          newPassword: this.newPassword
        }

        this.$Api.UserUpdatePassword(param).then((res) => {
          console.log(res);
          if(res.q.s == 0) {
            this.$toast('修改成功');
          }else {
            this.$toast(res.q.d);
          }
        })
      }
    }
  }
</script>


<style lang="scss">
  #editPassword {
    .input {
      position: relative;
      margin: 0 0.4rem;
      line-height: 1.2rem;
      height: 1.2rem;
      border-bottom: 1px solid #eee;

      input {
      	font-size: 0.4rem;
        width: 100%;
      }
    }
  }
</style>



